const getChecklistData = (id) => {
    let retVal = {id,}
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw(`getChecklistData() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    retVal.type = metaData.type,
    retVal.label = metaData.label

    // for assessment-with-options and decision types, lookup other checklist
    // items which have it's ID as their promptID and add them as an "items"
    // attribute
    if ([AWO, DECISION].includes(metaData.type)) {
        retVal.items = CHECKLIST_META_DATA.filter(rec => rec.promptID === id)
    }
    return retVal
}

const getTabLabel = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getTabLabel() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return { id: id, label: metaData.abbrLabel }
}

const getLabel = (id) => {
    const metaData = CHECKLIST_META_DATA.find((rec) => rec.id === id)
    if (metaData === undefined) {
        throw (`getLabel() id: ${id} not found in CHECKLIST_META_DATA`)
    }
    return metaData.label
}

