const mkPhase = ({ phaseType, phase, id,  items = [], subPhases = [], interventionsMenu = [], uiNav = true, dataEntry = true }) => {
    const headingID = `heading-${id}`
    let obj = getChecklistData(headingID)
    obj.id = id
    obj.type = phaseType
    obj.uiNav = uiNav
    obj.dataEntry = dataEntry
    if (subPhases.length && items.length ) {
        throw Error(`a ${phaseType} cannot have both subPhases AND checklist items`)
    }
    if (subPhases.length) {
        obj.subPhases = subPhases.map(sp => {
            let cld = getChecklistData(`heading-${sp}`)
            cld.id = sp
            return cld
        })
    } else {
        obj.items = items.map(it => {
            let cld = Object.assign({}, getChecklistData(it), {phase: phase})
            return cld
        })
    }
    return obj
}

const createPhase = ({id, items=[], subPhases=[], interventionsMenu=[], uiNav = true, dataEntry = true}) => {
    return mkPhase({ phaseType: PHASE, phase: id, id,items, subPhases, interventionsMenu, uiNav, dataEntry })
}

const createSubPhase = ({ id, phase, items = [], subPhases = [], interventionsMenu = [], uiNav = true, dataEntry = true }) => {
    return mkPhase({phaseType: SUB_PHASE, phase, id, items, subPhases, interventionsMenu, uiNav, dataEntry })
}

