export const PHASE_NAV = []
const phases = PHASE_DATA.filter(rec => rec.type === PHASE)
let subPhases = PHASE_DATA.filter(rec => rec.type === SUB_PHASE)
phases.forEach(phase => {
    let tmp = Object.assign({},phase)
    delete tmp.uiNav
    delete tmp.dataEntry
    delete tmp.items
    delete tmp.interventionsMenu
    if (tmp.subPhases) {
        let subs = []
        tmp.subPhases.forEach(sp => {
            let subPhase = subPhases.find(rec => rec.id === sp.id)
            if (!!subPhase) {
                let tmp2 = Object.assign({}, subPhase)
                delete tmp2.uiNav
                delete tmp2.dataEntry
                delete tmp2.items
                delete tmp2.interventionsMenu
                tmp2.phase = phase.id
                subs.push(tmp2)
            }
        })
        tmp.subPhases = subs
    }
    PHASE_NAV.push(tmp)
})

