import classnames from 'classnames';

import { MEDS } from './traumaScenario'

const Medications = ({ active, scenario }) => {
    const medications = scenario.correctMedicationDosages;

    const getValue = (medication, field) => {
        if (undefined === medications ||
            undefined === medications[medication] ||
            undefined === medications[medication][field]) {
            return ""
        }
        return medications[medication][field]
    }
    return (
        <div className={classnames("info-wrapper", {"scenario-view-hidden": !active})} id="medications">
            <h1>Medication Dosages</h1>

            <div className="meds-wrapper">
                <table>
                    <thead>
                        <tr>
                            <th>Medication</th>
                            <th>Initial Dosage</th>
                            <th>Repeat Dosage</th>
                            <th>Repeat Frequency</th>
                            <th>Max Dosage</th>
                        </tr>
                    </thead>
                    <tbody>
                        {MEDS.map(med =>
                            <tr key={`${med.id}-row`}>
                                <th>{med.label}</th>
                                <td>
                                    <input type="text"
                                           id={`correctMedicationDosages-${med.id}-initialDosage`}
                                           defaultValue={getValue(med.id, "initialDosage")}
                                    />
                                </td>
                                <td>
                                    <input type="text"
                                        id={`correctMedicationDosages-${med.id}-repeatDosage`}
                                        defaultValue={getValue(med.id, "repeatDosage")}
                                    />
                                </td>
                                <td>
                                    <input type="text"
                                        id={`correctMedicationDosages-${med.id}-repeatFrequency`}
                                        defaultValue={getValue(med.id, "repeatFrequency")}
                                    />
                                </td>
                                <td>
                                    <input type="text"
                                        id={`correctMedicationDosages-${med.id}-maxDosage`}
                                        defaultValue={getValue(med.id, "maxDosage")}
                                    />
                                </td>

                            </tr>
                        )}
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default Medications;