import classnames from 'classnames';

const OPQRST = ({ active, scenario }) => {
    const OPQRST = scenario.OPQRST;
    return (
        <div className={classnames("info-wrapper", {"scenario-view-hidden": !active})} id="opqrst">
            <h1>OPQRST</h1>

            <label>
                O: <input type="text" id="OPQRST-O" defaultValue={OPQRST?.O}/>
            </label>
            <label>
                P: <input type="text" id="OPQRST-P" defaultValue={OPQRST?.P}/>
            </label>
            <label>
                Q: <input type="text" id="OPQRST-Q" defaultValue={OPQRST?.Q}/>
            </label>
            <label>
                R: <input type="text" id="OPQRST-R" defaultValue={OPQRST?.R}/>
            </label>
            <label>
                S: <input type="text" id="OPQRST-S" defaultValue={OPQRST?.S}/>
            </label>
            <label>
                T: <input type="text" id="OPQRST-T" defaultValue={OPQRST?.T}/>
            </label>
        </div>
    )
}

export default OPQRST;