// this list will eventually be managed via TraumaScenario data-entry
export const MEDS = [
    { id: "Acetaminophen", label: "Acetaminophen" },
    { id: "Aspirin", label: "Aspirin" },
    { id: "Diazepam", label: "Diazepam" },
    { id: "Etomidate", label: "Etomidate" },
    { id: "Ketamine", label: "Ketamine" },
    { id: "Ketorolac", label: "Ketorolac" },
    { id: "Fentanyl", label: "Fentanyl" },
    { id: "Lorazepam", label: "Lorazepam" },
    { id: "Midazolam", label: "Midazolam" },
    { id: "MorphineSulfate", label: "Morphine Sulfate" },
    { id: "NitrousOxide", label: "Nitrous Oxide" },
]

export const updateSchema = (scen, tmpl) => {
    // copies over missing fields from tmpl while throwing away
    // stuff no longer in use
    // console.log("BEFORE", scen)
    let newScen = {
        id: scen.id,
        name: scen.name,
        scenarioType: "trauma",
        scenarioVersion: "V2",
    }
    newScen.info = (!!scen.info) ? scen.info : tmpl.info

    // FIXME: this should lookup actual findings in use from trauma-scenario
    // (like I am for correctMedicationDosages below)
    newScen.assessmentFindings = (!!scen.assessmentFindings) ? scen.assessmentFindings : tmpl.assessmentFindings

    newScen.SAMPLE = (!!scen.SAMPLE) ? scen.SAMPLE : tmpl.SAMPLE
    newScen.OPQRST = (!!scen.OPQRST) ? scen.OPQRST : tmpl.OPQRST
    newScen.vitalSigns = (!!scen.vitalSigns) ? scen.vitalSigns : tmpl.vitalSigns

    newScen.correctMedicationDosages = {}
    if (undefined === scen.correctMedicationDosages) {
        // so we don't have to check if scen.correctMedicationDosages is
        // defined in the loop below
        scen.correctMedicationDosages = {}
    }
    // create a record for each med in use, filtering out ones which no
    // longer are
    MEDS.forEach(med => {
        const val = scen.correctMedicationDosages[med.id]
        if (undefined !== val && "object" === typeof val) {
            newScen.correctMedicationDosages[med.id] = val
        } else {
            newScen.correctMedicationDosages[med.id] = {
                initialDosage: null,
                repeatDosage: null,
                repeatFrequency: null,
                maxDosage: null
            }
        }
    })
    // console.log("AFTER", newScen)
    return newScen
}
